import { useEffect } from 'react'
import { useNavigate, useLocation } from 'react-router-dom'
import question from '../assets/jsons/question.json'
import { Container, Button, Card, CardContent, CardHeader } from '@mui/material'
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import Stopwatch from '../components/Stopwatch';

export default function SingleQuestion() {
  const navigate = useNavigate();
  async function getMoviesFromApi() {
    try {
      let response = await fetch('https://raw.githubusercontent.com/kaditya97/geojson/master/cadaster.json');
      let responseJson = await response.json();
      console.log(responseJson)
    } catch (error) {
      console.error(error);
    }
  }
  useEffect(() => {
    getMoviesFromApi()
  }, [])
  return (
    <Container maxWidth="xl">
      <div className="title">
        <Button variant="text" startIcon={<ArrowBackIosNewIcon />} onClick={() => navigate('/')}>Back</Button>
        <Stopwatch />
      </div>
      <div>{question.map((e, index) => {
        return (
          <Card key={index}>
            <CardHeader title={e.question_en} />
            {/* <div>{e.question_en}</div> */}
            <div>{e.question_np}</div>
            <CardContent>
              <div>{e.options.map(e => {
                return (
                  <div>{e}</div>
                )
              })}</div>
              <div>Ans: {e.answer}</div>
            </CardContent>
          </Card>
        )
      })}</div>
    </Container>
  )
}
